@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.6rem;
  --typing-font: 3rem;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Raleway";
}
.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
/* .condiv class is for the right side content container*/
.condiv {
  padding: 100px;
  background-color: rgb(230, 230, 230);
  flex: 6;
  flex: var(--condiv-flex);
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

nav {
  padding: 60px 0px 0px 0px;
  flex: 1.5;
  flex: var(--sidebar-flex);
  /*
  background: rgb(238, 103, 143);
  background: linear-gradient(
    180deg,
    rgba(150, 150, 150, 1) 50%,
    rgba(0, 0, 888888880, 1) 80%
  );
  */
  background-image: url(/dklangevin.github.io/static/media/sunset_tree.91a538dc.jpeg);
  background-size: auto 100%;
}

nav ul {
  font-size: 1.6rem;
  font-size: var(--fontsize-nav);
  font-style: bold;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
}

nav ul li {
  padding: 10px 90px 10px 90px;
  margin-top: 25px;
  margin-bottom: 25px;
}

a {
  text-decoration: none;
  color: rgb(180, 180, 180);
}

li.active {
  background: rgba(150, 150, 150, 0.3);
}

li.active a {
  text-decoration: none;
  color: white;
  font-weight: bolder;
}

.portrait {
  border-radius: 50%;
  width: 300px;
  margin: 30px;
}

